.grid-container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    padding: 20px 20px;
    font-weight: bold;
    font-size: x-large;
  }

  .grid-element {
    /* border-style: solid; */
    /* border-style: solid; */
    border-style: outset;
    border-width: 0.01px;
    box-shadow: 10px 10px 8px #888888;
    border-right: 5px solid blue;
    /* border-bottom: 5px solid blue; */
    padding: 40px 20px;
    text-align: center;
    border-radius: 15px;
  }

  .grid-element:hover {
      box-shadow: 15px 15px 10px #888888;;
  }

  .textStyle {
      text-decoration: underline;
      font-weight: lighter;
      font-size: x-large;
  }